import { Link } from "gatsby";
import styled from "styled-components";
import React from "react";
import Title from '../components/Title/Title';
import ButtonCostum from '../components/Button/Button';

const ContainerStyled = styled.section`
  background-color: #7D8260;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: white;
  font-weight: 300;
  padding-top: 50px;
  padding-bottom: 50px;
`;

const ParalaksDiv = styled.section`
  background-image: url('https://uniquekiosk.com/wp-content/uploads/2019/03/optical-shop-furniture-1.jpg');
  background-attachment: fixed;
  background-position: 50% 0;
  background-repeat: no-repeat;
  background-size: cover;
  height: 20vh;
  max-height: 300px;
  min-height: 200px;
`;



const AboutUs = () => (
    <>
        <ContainerStyled className="container-fluid">
            <div className="container">
                <Title name="Troch o Nas" />
                <p>
                    TwojeWidzimisi czerpi dum z tego, e klienci uznaj nasz profesjonalizm, majc pene zaufanie do wiadczonych przeze nas usug. Oferuj rozwizani dostosowane do indywidualnych potrzeb klientw po atrakcyjnych cenach, ktre speniaj wszystkie oczekiwania odbiorcy.
                    Wsppracujemy ze wiatowymi dostawcami, ktrzy zapewniaj najlepsz jako
            </p>
                <ButtonCostum name="Kontakt" />
                <ButtonCostum name="Umw wizyt" full />
            </div>
        </ContainerStyled>
        <ParalaksDiv />
    </>
)


export default AboutUs
