import { Link } from "gatsby";
import styled from "styled-components";
import React from "react";
import Title from '../components/Title/Title';

const SectionColor = styled.section`
  background-color: #27211E;
  padding: 50px 0;
`;

const GridContainer = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-template-areas: "Area1 Area2" "Area3 Area4";


  @media all and (max-width: 768px){
	&{
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr;
        grid-template-areas: "Area1" "Area2" "Area4" "Area3";
	}
}

    .Area1 { grid-area: Area1; }

    .Area2 { grid-area: Area2; }

    .Area3 { grid-area: Area3; }

    .Area4 { grid-area: Area4; }
`;


const Paragraph = styled.p`
  color: white;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  text-align: center;
`;

const TextDiv = styled.div`
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  padding: 10px 50px;
  color: #66615F;
  font-weight: 300;
`;


const ImageDiv = styled.div`
    background-image: url('https://images.adsttc.com/media/images/5012/9a7f/28ba/0d67/1700/0893/newsletter/stringio.jpg?1414494789');
    background-repeat: no-repeat;
    background-size: cover;
    height: 700px;
    max-height: 50vh;
`;

const ImageDiv2 = styled.div`
    background-image: url('https://images.adsttc.com/media/images/5012/9a7f/28ba/0d67/1700/0893/newsletter/stringio.jpg?1414494789');
    background-repeat: no-repeat;
    background-size: cover;
    height: 700px;
    max-height: 50vh;
`;

const TextH2 = styled.h2`
  color: #7D8260;
  margin: 30px 0;
  font-size: 1.2rem;
  font-weight: 400;
`;

const TextH3 = styled.h3`
  color: rgb(39,33,30);
  margin: 30px 0;
  font-size: 1.0rem;
  font-weight: 400;
`;



const AboutUs = () => (
    <>
        <SectionColor>
            <div className="container">
                <Title name="Profesjonalne usugi" />
                <Paragraph>
                    Ekspert w zasigu rki
            </Paragraph>
            </div>
        </SectionColor>
        <section>
            <div className="jumbotron-fluid">
                <GridContainer>
                    <ImageDiv className="Area1">

                    </ImageDiv>
                    <TextDiv className="Area2">
                        <TextH2>Badania Wzroku</TextH2>
                        <TextH3>Najmniejsze szczegy robi najwiksz rnic</TextH3>

                        Skorzystaj z moich usug, a przekonasz si, e moesz otrzyma wysok jako stosunkowo niewielkim kosztem! Od lat wiadcz profesjonalne Usugi optyczne, a nabyte przeze mnie dowiadczenie i posiadana wiedza pozwalaj mi zapewnia najlepsze rozwizania w najlepszej cenie. W porwnaniu z konkurencyjnymi usugodawcami wyrniamy si wyjtkow dbaoci o szczegy i zdolnoci dostosowania swoich usug do indywidualnych potrzeb kadego klienta. Badania przeprowadzania s przez najbardziej dowiadczonych Optometrystw.
                    </TextDiv>
                    <TextDiv className="Area3">
                        <TextH2>Gwarancja</TextH2>
                        <TextH3>Gwarantujemy najlepsz jako widzenia...</TextH3>

                        Procedura przeprowadzania refrakcji, cho wydaj si skomplikowana i dokada, powinna by rwnie dopasowana do problemu wzrokowych pacjenta.  Opracowalimy wasne procedury dla rnych typw okularw tak aby wycign z nich 100%
                        Dlatego nawet okulary progresywne nie bd stanowiy dla Ciebie adnego kopotu
                    </TextDiv>
                    <ImageDiv2 className="Area4">

                    </ImageDiv2>
                </GridContainer>
            </div>
        </section>
    </>
)


export default AboutUs
